<style lang="css" scoped>
.el-select{
    width:120px;
}
</style>
<template>
<el-row>
    <el-col :span="22" :offset="1">
        <el-row>
            <el-col :span="8" :offset="1">
                <el-input placeholder="" size="small" v-model="asin" class="input-with-select">
                    <el-select v-model="country" slot="prepend" placeholder="">
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="2"></el-option>
                    <el-option label="" value="3"></el-option>
                    <el-option label="" value="4"></el-option>
                    <el-option label="" value="5"></el-option>
                    <el-option label="" value="6"></el-option>
                    <el-option label="" value="7"></el-option>
                    <el-option label="" value="8"></el-option>
                    </el-select>
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </el-col>
            <el-col :span="4" :offset="11">
                <el-button type="primary" size="small"></el-button>
                <el-button type="primary" size="small"></el-button>
            </el-col>
        </el-row>
        <el-row style="margin-top:50px;">
            <el-col :span="24">
                <el-table
                :header-cell-style="header"
                :data="tableData"
                border
                style="width: 100%">
                    <el-table-column
                    type="selection">
                        
                    </el-table-column>
                      <el-table-column
                      prop="country"
                      align="center"
                      label="">
                        
                      </el-table-column>
                      <el-table-column
                      prop="name"
                      align="center"
                      label="">

                      </el-table-column>
                   <el-table-column
                        prop="date"
                        align="center"
                        label="">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="">
                        <template slot-scope="scope">
                          <el-button type="primary" size="mini" icon="fa fa-edit fa-lg"></el-button>
                          <el-button type="danger" size="mini" icon="fa fa-trash-o fa-lg"></el-button>
                          <el-button size="mini" type="info"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
    </el-col>
</el-row>
</template>
<script>
export default {
  data () {
    return {
        header:{
            'backgroundColor':'#DCDFE6',
            'color':'black'
        },  
        country:'',
        asin:'',
        tableData: [{ 
            img: 'test.jpg',
            country:'',
            name:"Template",
            date:'2017-12-14'
          }]
          }
    },
  methods:{
      handleCommand(command){
          console.log(command)
      }
  }
}
</script>


