<style lang="css" scoped>
</style>
<template>
<el-row>
  <el-col :span="22" :offset="1">
    <el-row>
      <el-col :span="6">
        <el-input size="small" placeholder="/">
          <el-button slot="append" size="small"></el-button>
        </el-input>
      </el-col>
    </el-row>
    <el-row style="margin-top:30px;">
      <el-col :span="24">
        <el-table
        border
        :header-cell-style="header"
      :data="tableData">
      <el-table-column
      prop="shopname"
      align="center"
        label="">
      </el-table-column>
        <el-table-column
        prop="sellerId"
        label="">
      </el-table-column>
      <el-table-column
      prop="FBA"
      align="center"
        label="FBA">
      </el-table-column>
      <el-table-column
        prop="ordinary"
        align="center"
        label="">
      </el-table-column>
      <el-table-column
        prop="selfSupport"
        align="center"
        label="">
      </el-table-column>
      <el-table-column
      prop="price"
      align="center"
        label="">
      </el-table-column>
      <el-table-column
      prop="date"
        label="">
      </el-table-column>
      <el-table-column
        align="center"
        label="">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" icon="fa fa-mail-forward"></el-button>
        </template>
      </el-table-column>
    </el-table>
      </el-col>
    </el-row>
  </el-col>
</el-row>
</template>
<script>
export default {
  data () {
    return {
         header:{
            'backgroundColor':'#DCDFE6',
            'color':'black'
        },
        tableData: [{
            shopname:'THE BT GROUP',
            sellerId:'A2FQ5GG01HBOZ1',
            FBA:'',
            ordinary:'',
            selfSupport:'',
            price:'4.8',
            date:'2017-12-14 07:38:30'
            }]
    }
  }
}
</script>


