<template>
<section>
    <!--  -->
    <el-row style="margin-top:50px;">
        <el-col :span="10">
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="">
                    <el-select v-model="form.country" placeholder="">
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="2"></el-option>
                    <el-option label="" value="3"></el-option>
                    <el-option label="" value="4"></el-option>
                    <el-option label="" value="5"></el-option>
                    <el-option label="" value="6"></el-option>
                    <el-option label="" value="7"></el-option>
                    <el-option label="" value="8"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="ASIN">
                    <el-input v-model="form.asin" placeholder="ASIN"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.keyWords" placeholder=""></el-input>
                </el-form-item>
            </el-form>
        </el-col>
        
    </el-row>

    <!--  -->
    <el-row style="margin-top:50px;">
        <el-col :span="8">
            <el-table
            :data="tableData"
            :header-cell-style="TbHeaderStyle"
            border>
            <el-table-column
                prop="asin"
                label="ASIN"
                width="180">
            </el-table-column>
            <el-table-column
                prop="keyword"
                label=""
                width="180">
            </el-table-column>
            <el-table-column
                header-align="center"
                align="center"
                :class-name="impt"
                prop="rank"
                label="">
            </el-table-column>
            </el-table>
        </el-col>
    </el-row>

</section>
</template>
<script>
export default {
  data () {
    return {
        important:'impt',
        TbHeaderStyle:{
            'backgroundColor':'#DCDFE6',
            'color':'black'
        },
        form:{
            country:'',
            keyWords:'',
            asin:''
        },
        tableData: [{
            asin: 'B000WWK844',
            keyword: 'Jacquard',
            rank: '100+'
          }]
    }
  }
}
</script>
<style lang="css" scoped>
 .input-with-select .el-input-group__prepend {
    background-color: #fff;
  }
.impt .cell{
    color: red !important;
}
</style>


