<style lang="css" scoped>
.btline{
    padding-bottom: 10px;
    border-bottom: 1px solid #DFE4ED;
}
.myorder{
    color: #878D99;
    font-size: 14px;
}
.orderby{
    margin-top: 20px;
}
.line{
    text-align: center;
}
.more{
    color: #409EFF;
    font-size: 14px;
    float: right;
    margin-right:30px;
    cursor: pointer;
}
.el-form{
    font-size: 14px;
}
.right{
    float: right;
}
.rs-img{
    width: 100px;
    float: left;
    margin-right: 10px;
}
.rs-img img{
    width: 100%;
}
.fade-leave-to{
    height:0px;
    overflow: hidden;
}
.fade-enter-to{
    height:auto;
}
.fade-enter-active {
  transition: height 3s ease;
}
.fade-leave-active {
  transition: all 3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
</style>
<template>
<section>
    <el-row class="btline">
        <el-col :span="24">
            <span class="myorder"></span>
            <el-tag type="info" closable></el-tag>
            <el-tag type="info" closable></el-tag>
            <span class="more" @click="show()">{{ showbutton.name }} <i :class="['fa',showbutton.name===''?'fa-angle-down':'fa-angle-up']"></i></span>
        </el-col>
    </el-row>

    <!--  -->
    <el-row>
        <el-col :span="24" class="orderby btline" >
            <el-form ref="form" :inline="true" :model="form" class="demo-form-inline" label-width="100px" size='small'>
            <el-form-item label="">
                <el-select v-model="form.country" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="4"></el-option>
                <el-option label="" value="5"></el-option>
                <el-option label="" value="6"></el-option>
                <el-option label="" value="7"></el-option>
                <el-option label="" value="8"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-date-picker
                v-model="form.date"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="-"
                start-placeholder=""
                end-placeholder=""
                :picker-options="pickerOptions">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="Rank">
                <el-input v-model="form.minRank" style="width: 200px;" placeholder=""></el-input>
                -
                <el-input v-model="form.maxRank" style="width: 200px;" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-cascader :options="options" v-model="selectedOptions">
                </el-cascader>
            </el-form-item>
            <el-form-item label="Reviews">
                <el-input v-model="form.minReviews" style="width: 200px;" placeholder=""></el-input>
                -
                <el-input v-model="form.maxReviews" style="width: 200px;" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-radio-group v-model="form.sellerType" size="small">
                    <el-radio label="1" border>FBA</el-radio>
                    <el-radio label="2" border>FBM</el-radio>
                </el-radio-group>
            </el-form-item>
            <transition name="fade">
                <el-collapse-transition>
            <div class="hide" v-if="isShow">
                <el-form-item label="Asin">
                    <el-input v-model="form.asin" placeholder="Asin"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.minSalesVolume" style="width: 200px;" placeholder=""></el-input>
                    -
                    <el-input v-model="form.maxSalesVolume" style="width: 200px;" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.minSalesAmount" style="width: 200px;" placeholder=""></el-input>
                    -
                    <el-input  v-model="form.maxSalesAmount" style="width: 200px;" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.sellerId" placeholder=""></el-input>
                </el-form-item>
                 <el-form-item label="">
                    <el-input v-model="form.minPrice" style="width: 200px;" placeholder=""></el-input>
                    -
                    <el-input v-model="form.maxPrice" style="width: 200px;" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="Title()">
                    <el-input v-model="form.untitle" placeholder="Title()"></el-input>
                </el-form-item>
                 <el-form-item label="Title()">
                    <el-input v-model="form.title" placeholder="Title()"></el-input>
                </el-form-item>
                 <el-form-item label="">
                    <el-input v-model="form.brand" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.minFollowSell" style="width: 200px;" placeholder=""></el-input>
                    -
                    <el-input v-model="form.maxFollowSell" style="width: 200px;" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="Review">
                    <el-input v-model="form.minStarLevel" style="width: 200px;" placeholder=""></el-input>
                    -
                    <el-input v-model="form.maxStarLevel" style="width: 200px;" placeholder=""></el-input>
                </el-form-item>
            </div>
            </el-collapse-transition>
            </transition>
             <el-row type="flex" justify="end">
                    <el-col :span="2"><el-button  type="primary" @click="onSubmit"></el-button></el-col>
                    <el-col :span="2"><el-button @click="alert()"></el-button></el-col>
                    <el-col :span="2"><el-button></el-button></el-col>
            </el-row>
            </el-form>
        </el-col>
    </el-row>

    <!--  -->
    <el-row>
        <el-col :span="24">
        <el-table
        :data="tableData"
        style="width: 100%">
            <el-table-column
                label=""
                width="300">
                <template slot-scope="scope">
                    <div class="rs-img">
                        <img src="../../assets/test.jpg" alt="">
                    </div>
                    <div style="float:left;">
                        <span>{{ scope.row.name }}</span>
                        <br>
                        <span>{{ scope.row.asin }}</span>
                        <br>
                        <span>{{ scope.row.price }}</span>
                    </div>
                    
                </template>
            </el-table-column>

            <el-table-column
                prop="brand"
                label=""
                >
            </el-table-column>

            <el-table-column
                prop="rank"
                sortable
                label="Rank">
            </el-table-column>

            <el-table-column
                prop="reviews"
                sortable
                label="Reviews">
            </el-table-column>

            <el-table-column
                prop="seller"
                label="">
            </el-table-column>

            <el-table-column
                prop="address"
                label="">
            </el-table-column>
        </el-table>
        </el-col>
        <el-col :span="9" :offset="15" style="margin-top:20px;">
        <el-pagination
            :current-page="currentPage"
            :page-sizes="[100, 200, 300, 400]"
            :page-size="100"
            layout="total, sizes, prev, pager, next, jumper"
            :total="400">
        </el-pagination>
        </el-col>
    </el-row>
</section>

</template>
<script>
export default {
  data () {
    return {
        isShow:false,
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        showbutton:{
            name:''
        },
        form: { //
          country: '',
          date: '',
          minRank:'',
          maxRank:'',
          minReviews:'',
          maxReviews:'',
          sellerType:'1',
          asin:'',
          minSalesVolume:'',
          maxSalesVolume:'',
          minSalesAmount:'',
          maxSalesAmount:'',
          sellerId:'',
          minPrice:'',
          maxPrice:'',
          untitle:'',
          title:'',
          brand:'',
          minFollowSell:'',
          maxFollowSell:'',
          minStarLevel:'',
          maxStarLevel:''
        },
        options:[ //
            {
          value: 'zhinan',
          label: '',
          children: [{
            value: 'shejiyuanze',
            label: '',
            children: [{
              value: 'yizhi',
              label: ''
            }, {
              value: 'fankui',
              label: ''
            }, {
              value: 'xiaolv',
              label: ''
            }, {
              value: 'kekong',
              label: ''
            }]
          }, {
            value: 'daohang',
            label: '',
            children: [{
              value: 'cexiangdaohang',
              label: ''
            }, {
              value: 'dingbudaohang',
              label: ''
            }]
          }]
        }
        ],
        selectedOptions: [], //
        selectedOptions2: [],
        tableData: [{ 
            img: 'test.jpg',
            name: '',
            asin: '0062110721',
            price:'2.25',
            brand:'N.A.',
            rank:'135',
            category:'Sports & Outdoors',
            reviews:'535',
            stars:'4.7',
            seller:'XiaoMing'
          },
          {
            img: 'test.jpg',
            name: '',
            asin: '0062110721',
            price:'2.25',
            brand:'N.A.',
            rank:'135',
            category:'Sports & Outdoors',
            reviews:'535',
            stars:'4.7',
            seller:'XiaoMing'
          },
          {
            img: 'test.jpg',
            name: '',
            asin: '0062110721',
            price:'2.25',
            brand:'N.A.',
            rank:'135',
            category:'Sports & Outdoors',
            reviews:'535',
            stars:'4.7',
            seller:'XiaoMing'
          }],
          currentPage: 1
    }
  },
  methods: {
      onSubmit() {
        console.log('submit!');
      },
      show(){
          this.isShow = !this.isShow
          if(this.showbutton.name === ''){
            this.showbutton.name = ''
          }else{
              this.showbutton.name = ''
          }
      },
      alert() {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
        //   inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        //   inputErrorMessage: ''
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: ''
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });       
        });
      }
    }
}
</script>


